"use client";

import {
  Button,
  Column,
  Columns,
  NumberField,
  SelectField,
  TextField,
} from "@buildo/bento-design-system";

import useDeviceContext from "@/hooks/useDeviceContext";
import { produce } from "immer";
import { FaGear } from "react-icons/fa6";
import { MdDelete } from "react-icons/md";
import { TbCircuitPushbutton } from "react-icons/tb";

export const GrinderSettingComponent = ({
  key,
  index,
}: {
  key: number;
  index: number;
}) => {
  const { grinderSettings, setGrinderSettings } = useDeviceContext();
  console.log("Grinder/ create ", key);

  return (
    <div className="flex flex-col  space-y-4 rounded-xl bg-white p-4">
      <TextField
        name="ingredient"
        label="Ingredient"
        placeholder="What is the coffee bean ?"
        value={grinderSettings[index].name}
        onChange={(e: string) => {
          setGrinderSettings(
            produce(grinderSettings, (draft) => {
              draft[index].name = e;
            }),
          );
        }}
      />

      <div className="flex flex-grow flex-row justify-stretch space-x-4">
        <Columns space={8}>
          <Column width="4/5">
            <SelectField
              value={grinderSettings[index].connectionType}
              onChange={(e) => {
                setGrinderSettings(
                  produce(grinderSettings, (draft) => {
                    draft[index].connectionType = e as "Wire" | "Bluetooth";
                  }),
                );
              }}
              name="connectionType"
              label="Connection"
              placeholder="How is the grinder connected ?"
              options={[
                {
                  value: "wire",
                  label: "Wire",
                  kind: "two-line",
                  secondLine: "Direct wiring using the PID port",
                },
                {
                  value: "ble",
                  label: "Bluetooth",
                  kind: "two-line",
                  secondLine: "Pair to a Bluetooth scale",
                },
              ]}
            />
          </Column>
          <Column width="1/5">
            <NumberField
              name="ID"
              value={grinderSettings[index].connectionId}
              onChange={(e: number) => {
                setGrinderSettings(
                  produce(grinderSettings, (draft) => {
                    draft[index].connectionId = e;
                  }),
                );
              }}
              label="ID"
            />
          </Column>
        </Columns>
      </div>

      <div className="my-4 flex flex-row space-x-4">
        <Button
          label="Calibrate"
          onPress={() => {}}
          hierarchy="primary"
          kind="outline"
          icon={() => <FaGear />}
        />
        <Button
          label="Test"
          onPress={() => {}}
          hierarchy="primary"
          kind="outline"
          icon={() => <TbCircuitPushbutton />}
        />
        <Button
          label="Remove"
          onPress={() => {}}
          hierarchy="danger"
          kind="outline"
          icon={() => <MdDelete />}
        />
      </div>
    </div>
  );
};
