const CalibrateButton = ({ onClick }: { onClick: () => void }) => {
  return (
    <button
      onClick={(e) => {
        e.stopPropagation();
        onClick();
      }}
      className="flex h-8 w-fit items-center justify-center rounded-xl border-2 border-white p-4 text-white opacity-20"
    >
      Calibrate
    </button>
  );
};

const PidSettings = () => {
  const { t } = useTranslation();
  const [expanded, setExpanded] = useLocalStorage("pidSettings", false);
  const [isOpen, setModelOpen] = useState(false);

  const toggleExpand = () => {
    setExpanded(!expanded);
  };
  const cardRef = useRef<HTMLDivElement>(null);

  return (
    <Card
      ref={cardRef}
      className={
        "w-full max-w-2xl transform bg-gradient-to-r from-white to-white transition duration-500 select-none"
      }
    >
      <CardHeader
        className="flex h-16 flex-row items-center justify-between"
        onClick={toggleExpand}
      >
        <div className="flex items-center">
          <FaTemperatureHigh className="ml-4 text-black" />
          <h2 className="mx-4 font-bold text-black">{t("Heater")}</h2>
        </div>
        {expanded ? (
          <>
            <MdKeyboardArrowDown className="mr-4 text-black" />
          </>
        ) : (
          <MdKeyboardArrowRight className="mr-4 text-black" />
        )}
      </CardHeader>
      {expanded ? (
        <div className="p-4 space-y-4">
          <Banner
            kind="positive"
            title="What is Heater ?"
            description="There are 3 channel of PID-enabled heater. You can configure each channel with temperature setpoint. We support the following type of sensor: DS18B20, NTC(10K), NTC(100K). DS18B20 is strongly recommended"
          />

          <CardBody className="flex w-full flex-col justify-center  py-4">
            <PidConfigurationComponents index={0} />
            <PidConfigurationComponents index={1} />
            <PidConfigurationComponents index={2} />
          </CardBody>
          <CardFooter
            onClick={toggleExpand}
            className="align-center justify-center"
          >
            <MdKeyboardArrowUp className="m-4 text-black" />
          </CardFooter>
        </div>
      ) : null}

      {isOpen && (
        <Modal
          title="Calibrate flowmeter"
          size="small"
          onClose={() => setModelOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => setModelOpen(false),
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setModelOpen(false),
          }}
          autoFocus={false}
        >
          <Inset space={4}>
            <Body size="medium">Content</Body>
          </Inset>
        </Modal>
      )}
    </Card>
  );
};

